import React, {useState} from 'react'

import TODOContent from './TODOContent';

import * as actions from '../../actions';
import { connect } from 'react-redux';

function ToDOList(props){
    const [mode, setMode] = useState(true)
    
    let generateTodo = (todoList, id)=>{
      return todoList.map((todo)=>{
        console.log(todo)
       return <TODOContent myTodo = {todo} id={id}/>
      })
    }
    let generateLists = () => {
      let respArr = [];
      let chosen = props.list.chosen;
      respArr = props.list.todos.resp;
      console.log(props.list)
     return  respArr.map((data)=>{
        console.log(data)
        if(data.category === chosen){
          let todoList = data.todos;
          console.log(todoList)
          return (
            <div class="row m-2">
            <div class="col s12 m6">
              <div class="card green lighten-5">
                <div class="card-content">
                  <h4 style={{padding:0, margin:0}} class="green-text">{data.title}</h4>
                  <div class="pr-4 pl-4 mt-2">
                   
                     {generateTodo(todoList, data._id)}
                  
                    
                  
                  </div>
                </div>
                <div class="right-align">
                    <div class="btn m-1">Edit</div>
                    <div class="btn m-1">Delete</div>
                </div>
              </div>
            </div>
          </div>
          )
      
    }
    
      })
        
        
    }
    return (
        <div style={{float:'right'}} class="mt-2 ml-1 mr-1 card col col-sm-12 col-md-7 col-lg-7 col-xl-9">
            <div class="card-title">{props.list.chosen}</div>
           {generateLists()}
           
        </div>
        )
        
   
}

const mapStateToProps = (myState)=>{
  //console.log(myState);\
  let {auth, error, list} = myState;
  return{
      auth,
      error,
      list
  }
 
}


export default connect(mapStateToProps, actions)(ToDOList);
